/**
 * antdprops-type
 * @param { Array<string | number> } rawArray: 
 */
export function formatTableValue(rawArray = []) {
    return rawArray.reduce(function (result, item) {
        result.push({ value: item });
        return result;
    }, []);
}
/**
 * index
 */
export function sortIndex(rawArray) {
    if (rawArray.length <= 1) {
        return rawArray;
    }
    return rawArray.sort((a, b) => b - a);
}
